import React, {useState} from "react";
// import SearchWord from "./components/SearchWord";
import Search from "./components/Search";
import WordOfTheDay from "./components/DayWord";
import About from "./components/About";
import Navbar from "./components/Navbar";

export default function App() {
  const [theme, setTheme] = useState("dark");
  
  document.body.style.backgroundColor = theme === "dark" ? "#161b22" : "white";

  const boxStyle = {
    backgroundColor: theme === "dark" ? "#0d1117" : "white",
    border: theme === "dark"? '1px solid #30363d' : '1px solid #ccc',
    borderRadius: '6px'
  }
  return (
    <>
      <Navbar theme={theme} setTheme={setTheme} />
      <div className="container">
        <Search theme={theme} changeTheme={setTheme} boxStyle={boxStyle}/>
        <WordOfTheDay theme={theme} boxStyle={boxStyle}  />
        <About theme={theme} boxStyle={boxStyle} />
      </div>
    </>
  );
}
